@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600&display=swap');

.presurvey-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  min-height: 100vh; /* แก้เป็น min-height เพื่อให้รองรับเนื้อหาที่ยาวขึ้น */
  background-color: #fdf2f8; /* ปรับสีพื้นหลังให้อ่อนลง */
  padding: 2rem 1rem; /* ปรับ padding ให้เหมาะสม */
  width: 100%;
  max-width: 768px; /* *** กำหนดความกว้างสูงสุดที่ 768px *** */
  margin: 0 auto; /* *** จัดให้อยู่กึ่งกลางจอเสมอ *** */
  box-sizing: border-box;
  font-family: 'Prompt', sans-serif;
  opacity: 0; 
}

.presurvey-header {
  width: 100%;
  text-align: center;
  margin-bottom: 2rem;
  padding-bottom: 1.5rem;
  border-bottom: 2px solid #fbcfe8;
}

.presurvey-header h2 {
  font-size: 1.5rem; /* ปรับขนาด */
  color: #be185d; /* ปรับสี */
  font-weight: 600;
  margin-bottom: 1rem;
}

.instructions p, .scale-guide p {
  font-size: 0.9rem;
  color: #4b5563;
  text-wrap: balance; /* *** ช่วยตัดคำให้สวยงาม *** */
  max-width: 600px;
  margin: 0.5rem auto;
}

.survey-section {
  width: 100%;
  background-color: #ffffff;
  padding: 1.5rem;
  border-radius: 12px;
  box-shadow: 0 4px 6px -1px rgb(0 0 0 / 0.1), 0 2px 4px -2px rgb(0 0 0 / 0.1);
  margin-bottom: 2rem;
}

.survey-section h3 {
  font-size: 1.2rem;
  font-weight: 500;
  color: #9d174d;
  margin-bottom: 1.5rem;
  padding-bottom: 0.75rem;
  border-bottom: 1px solid #fce7f3;
}

.presurvey-field {
  margin-bottom: 1.5rem;
  width: 100%;
}

.presurvey-field:last-child {
  margin-bottom: 0;
}

.presurvey-field label {
  display: block;
  margin-bottom: 1rem;
  font-size: 1rem; /* ปรับขนาด */
  color: #1f2937;
  font-weight: 400; 
  line-height: 1.6;
  text-wrap: balance; /* *** ช่วยตัดคำให้สวยงาม *** */
}

.options {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 0.75rem; /* ปรับระยะห่าง */
}

.options label {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  border: 1px solid #fbcfe8;
  border-radius: 20px;
  cursor: pointer;
  transition: all 0.2s ease-in-out;
}

.options input[type="radio"] {
  /* FIX: Visually hide the input but keep it focusable for validation */
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
  pointer-events: none;
}

.options input[type="radio"]:checked + span {
  /* ไม่ต้องใช้ span แล้ว */
}

.options label:has(input[type="radio"]:checked) {
  background-color: #be185d;
  color: white;
  border-color: #be185d;
}

button[type="submit"] {
  margin-top: 1rem;
  padding: 0.75rem 2rem;
  font-size: 1rem;
  color: #FFFFFF;
  background-color: #db2777;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  font-family: 'Prompt', sans-serif;
  font-weight: 500;
}

button[type="submit"]:hover {
  background-color: #be185d;
}